<template>
  <v-container grid-list-md>
    <Toolbar></Toolbar>
    <v-layout row wrap>
      <v-flex v-for="item in data" :key="item.id" xs6 sm4 md3 xl2>
        <v-card :id="`item${item.id}`">
          <a :href="`/view/${item.id}`">
            <v-img
              :src="item.image"
              :alt="item.name"
              height="200"
              lazy-src="data:image/png;base64,"
          /></a>

          <v-card-title>
            <h2>
              <a :href="`/view/${item.id}`" :style="`color:${item.color}`">{{
                item.name
              }}</a>
            </h2>
          </v-card-title>

          <v-card-subtitle>${{ item.price }}</v-card-subtitle>

          <v-card-text>
            {{ item.shortDesc }}
          </v-card-text>

          <v-card-actions>
            <ItemActions
              :id="item.id"
              :deleteCallback="deleteCallback"
            ></ItemActions>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import DataService from "@/services/DataService";
import ItemActions from "@/components/ItemActions";

export default {
  components: { ItemActions },
  name: "Home",
  props: ["data"],
  methods: {
    deleteCallback() {
      this.getAllData();
    },

    getAllData() {
      DataService.getAll()
        .then((response) => {
          this.data = this.findTheFruit(response.data.data);
          // Create a short description property
          this.data = this.data.map((item) => {
            item["shortDesc"] =
              item["description"].length > 130
                ? item["description"].substring(0, 130) + "..."
                : item["description"];
            return item;
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    editItem(id) {
      this.$router.push({ name: "EditItem", params: { id: id } });
    },

    deleteItem(id) {
      let item = this.data.find((obj) => {
        return obj.id === id;
      });
      if (confirm("Are you sure you want to delete the " + item.name + "?")) {
        DataService.delete(id)
          .then(() => {
            this.getAllData();
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },

    findTheFruit(obj, item = []) {
      if (obj != null && typeof obj == "object") {
        Object.keys(obj).forEach((key) => {
          if (
            key === "isFruit" &&
            obj[key] === true &&
            Object.prototype.hasOwnProperty.call(obj, "id")
          ) {
            item.push(obj);
          } else {
            this.findTheFruit(obj[key], item);
          }
        });
      }
      return item;
    },
  },
  mounted() {
    this.getAllData();
  },
};
</script>

<style lang="scss">
@import "@/scss/main.scss";
</style>
